import React from 'react'
import { Link, Route, useParams, useRouteMatch } from 'react-router-dom'
import "./style.css"

const selectItems ={
    "apple":["Ipod","Mac","Iphone"],
    "microsoft":["Windows","Office packages","Xbox"],
    "google":["Android","Gmail","Search engine"]
        
}
function createList(product){
    return (
        <li>{product}</li>
    );
}

const Product=()=>{
    const {companyName} =useParams();
    const cc= (selectItems[companyName]).map(createList);
    return(
        <div>
            <h3> <span className="capi">{companyName}</span>'s Products are:</h3>
            <ul>
                {cc}
            </ul>
        </div>
    );
}

function Categories() {
    const {url,path}=useRouteMatch();

    return (
        <div>
            <h1>This is Categories page.</h1>

            <ul>
                <li>
                    <Link to={`${url}/apple`}>Apple</Link>
                </li>
                
                <li>
                    <Link to={`${url}/microsoft`}>Microsoft</Link>
                </li>   
                
                <li>
                    <Link to={`${url}/google`}>Google</Link>
                </li>
            </ul>

            <Route path={`${path}/:companyName`}>
                <Product />
            </Route>

        </div>
    )
}

export default Categories;
